"use client";
import { useState, useEffect } from "react";
import { Gauge } from "@/components/Gauge";
import { Line } from "react-chartjs-2";
import { io } from "socket.io-client";

export default function Dashboard() {
  const [force, setForce] = useState(0);
  const [zpos,  setZpos]  = useState(0);
  const [points, setPoints] = useState<{t:number,f:number,z:number}[]>([]);

  useEffect(() => {
    const socket = io(process.env.NEXT_PUBLIC_WS_URL ?? "http://localhost:4000");
    socket.on("telemetry", (data) => {
      setForce(data.force);
      setZpos(data.z_position);
      setPoints(p => [...p.slice(-499), { t: Date.now(), f: data.force, z: data.z_position }]);
    });
    return () => socket.close();
  }, []);

  const chartData = {
    labels: points.map(p => new Date(p.t).toLocaleTimeString()),
    datasets: [
      { label: "Force [kN]", data: points.map(p => p.f), borderWidth: 1, yAxisID: "y1" },
      { label: "Z Pos [mm]", data: points.map(p => p.z), borderWidth: 1, yAxisID: "y2" },
    ],
  };

  return (
    <div className="grid lg:grid-cols-3 gap-6">
      <div className="col-span-1 flex flex-col gap-6">
        <Gauge value={force} max={50} label="Force [kN]" />
        <Gauge value={zpos}  max={100} label="Z Position [mm]" />
      </div>
      <div className="col-span-2 bg-white rounded-xl p-4 shadow">
        <Line data={chartData} options={{
          scales: { y1: { type: "linear", position: "left" }, y2: { type: "linear", position: "right" } },
          animation: false,
        }}/>
      </div>
    </div>
  );
}
